body {
  color: #420420;
  background-color: #e3dcf1;
  font-family: Arial, Helvetica, sans-serif;
}

img {
  max-width: 500px;
  max-height: 650px;
  margin: 0 auto;
}

h3 {
  text-align: center;
}

button {
  color: #420420;
  padding: 7px;
  border: solid 1px #420420;
  border-radius: 10px;
  border-style: double;
  cursor: pointer;
  background-color: #e3dcf1;
}

.main-box {
  width: 600px;
  position: absolute;
  margin: 15px 0;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  text-align: center;
}

.input-box {
  margin: 20px 0;
  font-size: 15px;
}

input {
  width: 250px;
  margin: 8px 0 10px 0;
  padding: 8px;
  border-radius: 7px;
}

.text-box {
  width: 500px;
  position: relative;
  margin: 0 auto;
  color: #000;
}

.top-text,
.bottom-text {
  width: 500px;
  position: absolute;
  text-align: center;
  margin: 0 auto;
  padding: 15px 0;
  overflow-wrap: break-word;
  font-size: 30px;
}

.top-text {
  top: 0;
}

.bottom-text {
  bottom: 0;
}

.col1,
.col2,
.col3,
.col4 {
  border: 0;
  border-radius: 100%;
  width: 20px;
  height: 20px;
  margin-right: 5px;
  cursor: pointer;
  vertical-align: middle;
}

.col1 {
  background-color: #ed1c25;
}

